import React, {  } from 'react'
import { Card, Typography, Grid, CardContent } from '@material-ui/core';
import colores from '../../styles/colors';




const GeneroContent = () => {


    return <>
     <Grid container spacing={3} style={{marginBottom: 30}} >
         <Grid item sm={10}>
               <Card  >
                 <CardContent>
                    <Typography>
                    Desde mi lugar, formo parte de la lucha hacia la paridad, inclusión, empoderamiento e igualdad de oportunidades para mujeres e identidades disidentes de nuestra ciudad.
                 </Typography>
                 </CardContent>
              
             </Card>
         </Grid>
         <Grid item sm={2} >
             <Card style={{height: 180, backgroundColor: colores.ejesPurple}}>
                 <CardContent>
                    <Typography style={{fontSize: 20}} >
                  
                     Contra violencia de Genero
                 </Typography>
                 <Typography style={{fontSize: 45}} >
                     *999
                
                 </Typography>
                 </CardContent>
              
             </Card>
         </Grid>
     </Grid>
    </>
}


export default GeneroContent;