import React, { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Box, Grid, IconButton, makeStyles } from '@material-ui/core';
import { FacebookProvider, Page, EmbeddedPost, CustomChat } from 'react-facebook';
import AmbienteContent from './ejesContent/ambiente';
import ObrasContent from './ejesContent/obras';
import GeneroContent from './ejesContent/genero';
import BarriosContent from './ejesContent/barrios';
import EjesContent from '../custom/ejesContent'
import Grow from '@material-ui/core/Grow';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import gen from '../assets/ejes/genn.jpg';
import obr from '../assets/ejes/obr.png';
import ecoAm from '../assets/ejes/maff.png';
import noi from '../assets/noimg.jpg';
import colores from '../styles/colors';
import port from '../assets/portyt.png'
import begIcon from '../assets/ejesProv/begicon.jpg'


const EjesModal = ({ open, handleClose }) => {
	const [swCont, setSwCont] = useState('barrios');
	const classes = useStyles();

	function switchContent(cont){
		switch (cont) {
			case 'ambiente':
				return <AmbienteContent />
				break;

			case 'deporte':
				return <ObrasContent />
				break;

			case 'genero':
				return <GeneroContent />
			break;

			case 'barrios':
				return <BarriosContent />
			break;

			default:
				return <></>
				break;
		}
	}

	
	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			
			>
				<AppBar className={classes.appBar}>
					<Toolbar style={{backgroundColor: colores.mainLightfull}}>
						<IconButton
							edge='start'
							color={colores.mainDarkfull}
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Ejes De Trabajo
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{ padding: isMobile ? 20 : 90, paddingTop: 40, backgroundColor: colores.fondoLight, textAlign: 'center' }}>
					<ContentEjes setSwCont={setSwCont} swCont={swCont} />
					
					<Divider style={{marginTop: 20, marginBottom: 20}} />
					<EjesContent>
						{switchContent(swCont)}
					</EjesContent>
				
					<ContentFacebook />
				
				</div>
			</Dialog>
		</div>
	);
};

const ContentFacebook = () => {

	const FbPost = [
		'https://www.facebook.com/jorgelinamudallel/posts/4102542703165503',
		'https://www.facebook.com/jorgelinamudallel/posts/4157726427647130',
		'https://www.facebook.com/jorgelinamudallel/posts/4195750497178056',
	//	'https://www.facebook.com/jorgelinamudallel/posts/4177032202383219'
	]

	return <FacebookProvider appId="865953314130137" >
	<Grid container spacion={3} style={{alignContent: 'center'}}>
		{FbPost.map((post) => (
			<Grid item  sm={4}>
			<EmbeddedPost width={isMobile ? 200 : 250}  href={post} />
			</Grid>
		))}
		
		
		
	</Grid>
	</FacebookProvider> 
}

const ContentEjes = ({setSwCont, swCont}) => {
	return (<>
		
		<Grid container spacing={3}   >
			{ejesC.map((eje, i) => (
					 <Grow  in={true} mountOnEnter  timeout={400 * i + 400}>
				<Grid item sm={3}>
					<CardEjes eje={eje} setSwCont={setSwCont} swCont={swCont}  />
				</Grid>
					</Grow>
			))}
		</Grid>
	</>);
};



const CardEjes = ({eje, setSwCont, swCont}) => {
	return (
		<Card  elevation={3} style={{ borderRadius: 15, textAlign: 'center', backgroundColor: swCont === eje.id ? eje.color : colores.mainLight}} onClick={() => setSwCont(eje.id)}>
			<CardActionArea>
				<CardMedia
					style={{ height: 150, marginTop: 15,marginRight: 15, marginLeft: 15, borderRadius: 15 }}
					image={eje.img}
					title={eje.title}
				/>
				<CardContent style={{maxHeight: 220, overflow: 'auto',/*  minHeight: 200 */}}>
					<Typography gutterBottom variant='h5' style={{fontSize: 20}}>
						{eje.name}
					</Typography>
				
				</CardContent>
			</CardActionArea>
			
		</Card>
	);
};

const ejesC = [
	{ id: 'ambiente', name: 'Medio Ambiente', img: ecoAm, color: colores.cardGradiant2, borde: colores.cardGrTex2 },
	{ id: 'deporte',  name: 'deportes', img: obr, color: colores.cardGradiant3, borde: colores.cardGrTex3 },
	{ id: 'genero',  name: 'Género', img: gen, color: colores.cardGradiant1, borde: colores.cardGrTex1 },
	{ id: 'barrios',  name: 'Barrios', img: noi, color: colores.cardGradiant4, borde: colores.cardGrTex4 },
];

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	appBar: {
		position: 'relative',
	},

	title: {
		marginLeft: 5,
		color: colores.mainDarkfull
	},
}));

export default EjesModal;
