import React, { forwardRef } from 'react';
import { isMobile } from 'react-device-detect';
import { Grid, IconButton, makeStyles, Box  } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import AppBar from '@material-ui/core/AppBar';
import colores from '../styles/colors'
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import obras from '../assets/ejes/obr.png';
import vacuna from '../assets/ejesProv/vacSF.png';



const MyModal = ({ open, handleClose }) => {
	const classes = useStyles();

	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			
			>
				<AppBar className={classes.appBar}>
					<Toolbar style={{backgroundColor: colores.mainLightfull}}>
						<IconButton
							edge='start'
							color={colores.mainDarkfull}
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Accion Con Provincia
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{ padding: isMobile ? 20 : 90, paddingTop: 40, backgroundColor: colores.fondoLight }}>
					<ContentJor/>
				</div>
			</Dialog>
		</div>
	);
};

const ContentJor = () => {
	return (
		<Grid  container spacing={3}   >
			<Grid item xs={12} sm={2}  >
		
			<ProvCard img={billetera} />
			<ProvCard img={vacuna} />
			<ProvCard  img={begIcon}/>
			<ProvCard img={begIcon} />
			<ProvCard img={obras} />
		
			</Grid>
            <Grid item xs={12} sm={9} wrap="nowrap">
				<Card style={{height: '100%'}}>HOLA</Card>
			</Grid>
			
		</Grid>
	);
};

const ProvCard = ({img}) => {

	return <Card elevation={3} style={{ marginBottom: 6, padding: 6, borderRadius: 6}}>
	
	<CardMedia style={{ height: '100%', width: '100%', height: 100 }}
		image={img}
		title={'Boleto Educativo Gratuito'} />		
	
	</Card>
}

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	appBar: {
		position: 'relative',
	},

	title: {
		marginLeft: 5,
		color: colores.mainDarkfull
	},
}));

export default MyModal;
